}

.NavBar_containerMenu__4wYPn {
  position: absolute;
  background: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  transition: all 500ms ease;
  opacity: 0;
  visibility: hidden;
}

.NavBar_btnId__1nBBp:checked~.NavBar_containerMenu__4wYPn {
  z-index: 2;
  opacity: 1;
  visibility: visible;
  position: fixed;
}

.NavBar_contMenu__3aLQH {
  z-index: 1;
  width: 100%;
  max-width: 250px;
  background: #1c1c1c;
  height: 100%;
  position: fixed;
  transition: all 500ms ease;
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
}

.NavBar_btnId__1nBBp:checked~.NavBar_containerMenu__4wYPn .NavBar_contMenu__3aLQH {
  -webkit-transform: translateX(50%);
          transform: translateX(50%);
}

.NavBar_contMenu__3aLQH nav {
  -webkit-transform: translateY(15%);
          transform: translateY(15%);
}

.NavBar_contMenu__3aLQH nav a {
  display: block;
  text-decoration: none;
  margin: 10px auto;
  padding: 20px;
  color: #c7c7c7;
  border-left: 5px solid transparent;
  transition: all 400ms ease;
}

.NavBar_contMenu__3aLQH nav a:hover {
  border-left: 5px solid #c7c7c7;
  background: #1f1f1f;
}

.NavBar_contMenu__3aLQH label {
  position: absolute;
  right: 15px;
  top: 10px;
  color: #fff;
  cursor: pointer;
  font-size: 18px;
}

